<template>
  <textarea
    ref="textarea"
    v-model="value"
    :rows="props.rows"
    class="w-full resize-none rounded-sm border bg-gray-50 p-2 outline-none transition focus:!border-blue-700 dark:border-gray-400 dark:bg-gray-600"
  />
</template>

<script setup lang="ts">
const { textarea, input } = useTextareaAutosize({ styleProp: "minHeight" })

const props = defineProps<{
  modelValue: string
  rows?: number
}>()

const emit = defineEmits<{
  (e: "update:modelValue", value: string): void
}>()

const value = computed({
  get() {
    return props.modelValue
  },
  set(newValue: string) {
    emit("update:modelValue", newValue)
    input.value = newValue
  },
})
</script>
